.cardWork {
    height: calc(100% - 220px);
    margin-top: 110px;
    margin-right: 178px;
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .info {
        height: 100%;
        padding-left: 80px;
        padding-right: 80px;
        position: relative;
        z-index: 1;
        top: 0;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .title {
            display: flex;
            flex-direction: column;
            align-items: center;

            .workNr {
                top: -47px;
                left: -180px;
                position: absolute;
                font-weight: bold;
                font-size: 220px;
                z-index: 1;
                letter-spacing: -20px;
                cursor: default;
            }

            h1 {
                font-size: 40px;
                margin: 0;
                width: 100%;
                text-align: center;
            }
        }
    }

    .overlay {
        position: absolute;
        top: 0;
        z-index: -1;
        height: 100%;
        width: 100%;
        opacity: 0.8;
    }

    .img {
        position: absolute;
        top: 0;
        z-index: -2;
        height: 100%;
        width: 100%;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            transition-timing-function: ease-in-out;
            transition-duration: 0.4s;
            transform: rotate(0deg);

            min-width: 110%;
            min-height: 110%;
            display: block;
            object-fit: cover;
            filter: grayscale(100%);
        }
    }

    &:hover {
        .img {
            img {
                transition-duration: 0.4s;
                transition-timing-function: ease-out;
                min-width: 115%;
                min-height: 115%;
                // transform: rotate(-1deg);
            }
        }
    }
}

@media only screen and (max-width: $screenS) {
    .cardWork {
        height: 100%;
        width: calc(100% - 60px);
        margin: 30px 30px 20px 30px;
        padding: 100px 10px 100px 10px;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .info {
            height: 100%;
            padding-left: 80px;
            padding-right: 80px;
            position: relative;
            z-index: 1;
            top: 0;

            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            .title {
                .workNr {
                    top: -125px;
                    left: -135px;
                    position: absolute;
                    font-weight: bold;
                    font-size: 110px;
                    z-index: 1;
                    letter-spacing: -10px;
                    cursor: default;
                }

                h1 {
                    font-size: 30px;
                }
            }
        }

        .overlay {
        }

        .img {
            img {
            }
        }

        &:hover {
            .img {
                img {
                }
            }
        }
    }
}
