.imgBG{
    position: relative;
}

.imgCon{
    height: 100%;
    padding-top: 110px;
    padding-bottom: 110px;
    margin-right: 178px;
    position: relative;

    display: flex;
    // align-items: center;
    justify-content: center;
    overflow: hidden;

    img {
        min-width: 100%;
        min-height: 100%;
        display: block;
        object-fit: cover;
    }
}

@media only screen and (max-height: 800px) {
    .imgCon{
        padding-top: 0px;
        padding-bottom: 0px;
    }
}

@media only screen and (max-width: $screenS) {
    .imgCon{
        width: 100%;
        padding: 0px 15px 0px 15px;
        margin: 0 0 20px 0;
        position: relative;
    
        display: flex;
        // align-items: center;
        justify-content: center;
        overflow: hidden;
    }
}